<template>
  <div class="default-address__container">
    <div class="default-address__address">
      <h3 class="default-address__address-title">
        {{ $t("billingAddress") }}
      </h3>
      <div class="default-address__address-content">
        {{ billingAddress.firstname + " " + billingAddress.lastname }}
      </div>
      <div class="default-address__address-content">
        {{ billingAddress.company }}
      </div>
      <div class="default-address__address-content">
        {{ billingAddress.street }}
      </div>
      <div class="default-address__address-content">
        {{
          renderCityAndPostalCode(billingAddress.city, billingAddress.postcode)
        }}
      </div>
      <div class="default-address__address-content">
        {{ billingAddress.country_id }}
      </div>
      <div class="default-address__address-content">
        T: {{ billingAddress.telephone }}
      </div>

      <router-link
        :to="{ path: `edit/id/${billingAddress.entity_id}` }"
        append
        class="btn-navigate-link btn-change-address"
        >{{ $t("changeAddress") }}</router-link
      >
    </div>
  </div>
</template>

<i18n>
{
  "is": {
    "billingAddress": "Greiðslukvittanir stílist á:",
    "changeAddress": "Breyta heimilisfangi"
  },
  "en": {
    "billingAddress": "Default Billing Address",
    "changeAddress": "Change Address"
  }
}
</i18n>

<script>
export default {
  name: "DefaultAddressContainer",
  props: {
    billingAddress: Object,
  },
  computed: {},
  methods: {
    renderCityAndPostalCode(city, postalCode) {
      const arr = [city, postalCode];
      return arr.filter(v => v && v !== "-").join(", ");
    },
  },
};
</script>

