<template>
  <div class="account-address__container account-page">
    <div class="account-address__header-title account-page__title">
      <h2>{{ $t("pageTitle") }}</h2>
    </div>

    <div class="account-address__content" v-if="dataReady">
      <div class="account-address__title-text-container">
        <h1 class="account-address__title-text">{{ $t("defaultAddress") }}</h1>
      </div>
      <DefaultAddressContainer :billingAddress="billingAddress" />

      <div class="account-address__add-btn" v-if="dataReady">
        <router-link :to="{ path: 'new' }" append>
          <base-button class="primary">{{
            $t("addNewAddress")
          }}</base-button></router-link
        >
      </div>

      <div class="account-address__title-text-container">
        <h1 class="account-address__title-text">
          {{ $t("additionalAddress") }}
        </h1>
      </div>
      <ListAddressContainer />
    </div>
  </div>
</template>

<i18n>
{
  "is": {
    "pageTitle": "Sendingarupplýsingar",
    "defaultAddress": "Heimilisföng",
    "additionalAddress": "Fleiri heimilisföng"
  },
 "en": {
    "pageTitle": "Address Book",
    "defaultAddress": "Default Addresses",
    "additionalAddress": "Additional Address Entries"
  }
}
</i18n>

<script>
import { mapGetters } from "vuex";
import DefaultAddressContainer from "@/components/ViewAddress/DefaultAddressContainer";
import ListAddressContainer from "@/components/ViewAddress/ListAddressContainer";
import BaseButton from "@/components/BaseButton";

export default {
  name: "ViewMyAccountAddress",
  components: { DefaultAddressContainer, ListAddressContainer, BaseButton },
  computed: {
    ...mapGetters(["listAddress"]),
    dataReady: function() {
      return this.$store.state.customer;
    },
    billingAddress() {
      return this.listAddress.find(address => address.default_billing === "1");
    },
  },
  mounted() {
    this.$store.dispatch("loadCart");
  },
};
</script>

