<template>
  <div class="list-address__container">
    <table class="table">
      <thead>
        <tr>
          <th>{{ $t("address") }}</th>
          <th class="text-center">{{ $t("action") }}</th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="address in listAddress" :key="address.entity_id">
          <td class="w-80 list-address__item-container">
            <p class="list-address__item-name">
              {{ address.firstname + " " + address.lastname }}
            </p>
            <p class="list-address__item-address">
              {{ getAddress(address.street, address.city, address.postcode) }}
            </p>
          </td>
          <td class="w-20">
            <div class="list-address__icon-buttons text-center">
              <router-link
                :to="{ path: `edit/id/${address.entity_id}` }"
                append
                class="list-address__btn-edit"
              >
                <i class="icon icon--edit"></i>
              </router-link>
            </div>
          </td>
        </tr>
      </tbody>
    </table>
  </div>
</template>

<i18n>
{
  "is": {
     "action": "Aðgerð",
    "address": "Heimilisfang"
  },
 "en": {
    "action": "Action",
    "address": "Address"
  }
}
</i18n>

<script>
import { mapGetters } from "vuex";
export default {
  name: "ListAddressContainer",
  computed: {
    ...mapGetters(["listAddress"]),
  },
  methods: {
    getAddress(street, city, postcode) {
      return [street, city, postcode]
        .filter(address => address && address !== "-")
        .join(", ");
    },
  },
};
</script>

